import './News.css';
import React, {Component} from 'react';
import Global from '../Global';
import UncheckedNews from '../uncheckedNews/UncheckedNews';
import CheckedNews from '../checkedNews/CheckedNews';
import {io} from 'socket.io-client';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios';
import Badge from '@material-ui/core/Badge';
import { TextField } from '@material-ui/core';
import Swal from 'sweetalert2';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import SearchOutlined from '@material-ui/icons/SearchOutlined';

import FormGroup from '@material-ui/core/FormGroup';

class News extends Component{
 
    
    constructor(props){
        super(props);
        this.url = Global.url;
        this.ws = Global.ws;
        this.socket = null;
        this.searchValue = '';
        this.checkedForms = React.createRef();
        this.uncheckedForms = React.createRef();
        this.searchState= false;
        this.filterState=false;
        this.state ={
            toast: null,
            uncheckedLength: '0',
            checkedAprovedLength: '0',
            checkedDeniedLength: '0',
            user: {},
            searchedForms:[],
            uncheckedForms:[],
            checkedForms:{},
            checkedA:true,
            checkedB:false,
            checkedC:false,
            uncheckedView:true
        };
    }
    componentWillMount(){
        this.veryfyUserSesion();
        toast.clearWaitingQueue();
    }
    getUser = (id) => {
        axios.get(this.url+'get-admin/'+id)
        .then(res => {
            this.setState({
                user: res.data.data
            });
        })
    }

    veryfyUserSesion = () =>{
        const sesionToken = localStorage.getItem('userSesion');
        axios.get(this.url+'auth-user', {
            headers:{
                'x-acces-token':sesionToken
            }
        })
        .then(res => {
            if(res.data.status !== 'Ok'){
                Swal.fire({
                    title: '<div class="big-square"></div>'+
                    '<div class="little-square"></div>'+
                    '<div class="bg-title-pop"><div class="list-label pop absolute-center"><p>Para acceder a la funcionalidad, debe iniciar sesión.</p></div></div>',
                    confirmButtonText: 'Seguir',
                    allowOutsideClick: false,
                    showCloseButton: true
                }).then(()=>{
                    this.props.history.push('/login');
                })
            }else{
                this.getUser(res.data.data.id);
                this.connectSocket();
            }
        },
        error =>{
            Swal.fire({
                title: '<div class="big-square"></div>'+
                '<div class="little-square"></div>'+
                '<div class="bg-title-pop"><div class="list-label pop absolute-center"><p>Para acceder a la funcionalidad, debe iniciar sesión.</p></div></div>',
                confirmButtonText: 'Seguir',
                allowOutsideClick: false,
                showCloseButton: true
            }).then(()=>{
                this.props.history.push('/login');
            })
        })
    }

    connectSocket = () => {
        this.socket = (io(this.ws));
        this.socket.on("get-news", data => {
            this.setState({
                toast: data
            });
            
            this.notify();
        });
    }

    changeState = (e,data) =>{
        var target = e.currentTarget;
        var activeNav =  document.querySelector('.btn-nav.active');
        var activeContainer = document.querySelector('.active.content-news');
        var unactiveContainer = document.querySelector('.inactive.content-news');
        
        var searchString= document.getElementById('outlined-basic');
        searchString.value='';
        if(this.state.uncheckedView){
            if(data){
                this.searchState=false;
                
                this.filterState=false;
                this.setState({
                    checkedForms:this.checkedForms.current.state.forms
                });
                this.setState({
                    uncheckedView:false
                },()=>{
                    activeContainer.className='unchecked-forms content-news inactive';
                    unactiveContainer.className='checked-forms content-news active';
                    
                    if(this.state.uncheckedForms.length!==0){
                        this.uncheckedForms.current.setState({
                            forms:this.state.uncheckedForms
                        });
                    }
                });
            }
        }else{
            if(!data){
                this.searchState=false;
                
                this.filterState=true;
                    this.setState({
                        uncheckedForms:this.uncheckedForms.current.state.forms
                    });
                this.setState({
                    uncheckedView:true
                },()=>{
                    activeContainer.className='checked-forms content-news inactive';
                    unactiveContainer.className='unchecked-forms content-news active';
                    this.checkedForms.current.setState({
                        forms:this.state.checkedForms
                    });
                })
            }
        }
        if(target.classList[1]!=='active'){
            if(target.classList[0]==='btn-nav-news'){
                target.className='btn-nav-news active btn-nav';
                activeNav.className='btn-nav-checked btn-nav';
            }else{
                target.className='btn-nav-checked active btn-nav';
                activeNav.className='btn-nav-news btn-nav';
            }
        }
    }

    notify = () => {
        toast.info('Nueva petición', {
            position: "bottom-right",
            autoClose: 15000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }

    getUncheckedLength= (data) =>{
        this.setState({
            uncheckedLength: data
        });
    }

    getCheckedLength= (data) =>{
        this.setState({
            checkedDeniedLength: data.denied.length,
            checkedAprovedLength: data.aproved.length,
        });
    }

    logOut = () =>{
        //remove tokenSesion
        localStorage.removeItem('userSesion');
         Swal.fire({
            title: '<div class="big-square"></div>'+
            '<div class="little-square"></div>'+
            '<div class="bg-title-pop"><div class="list-label pop absolute-center">Sesión cerrada</div></div>',
        }).then(()=>{
            this.props.history.push('/');
        })
        //redirectTo Home
    }
    handleChange = (e,data) => {
        e.preventDefault();
        debugger
        var searchedForms= this.state.searchedForms;
        
        var searchString= document.getElementById('outlined-basic');
        searchString.value='';
        debugger
        var formss;
        if(data===0){
            if(this.filterState){
                if(e.currentTarget.checked){
                    this.setState({
                        checkedA:true
                    },()=>{
                        debugger
                        this.checkedForms.current.setState({
                            forms:[]
                        },()=>{
                            this.checkedForms.current.setState({
                                forms:this.state.checkedForms
                            })
                            debugger
                        });
                    });
                }
                this.searchState=false;
                this.searchValue='';
            }
           
        }else if(data===1){
            debugger
            
            this.searchValue='';
            if(!this.filterState){
                this.filterState=true;
                this.searchState=true;
                if(e.currentTarget.checked){
                    this.setState({
                        checkedB:true
                    },()=>{
                        debugger
                        formss=this.state.checkedForms;
                        searchedForms=[];
                        this.checkedForms.current.setState({
                            forms:[]
                        },()=>{
                            formss.map((form,i)=>{
                                if(form.state){
                                    
                                    return searchedForms.push(form)
                                }
                            });
                            this.checkedForms.current.setState({
                                forms:searchedForms
                            });
                            debugger
                        });
                    });
                }
            }else{
                
                this.searchValue='';
                if(e.currentTarget.checked){
                    this.setState({
                        checkedB:true
                    },()=>{
                        formss=this.state.checkedForms;
                        searchedForms=[];
                        this.checkedForms.current.setState({
                            forms:[]
                        },()=>{
                            debugger
                            formss.map((form,i)=>{
                                if(form.state){
                                    
                                    return searchedForms.push(form)
                                }
                            });
                            this.checkedForms.current.setState({
                                forms:searchedForms
                            });
                            debugger
                        });
                    });
                }
            }
           
        }else if(data===2){
            
            this.searchValue='';
            debugger
            if(!this.filterState){
                this.filterState=true;
                this.searchState=true;
                if(e.currentTarget.checked){
                    this.setState({
                        checkedC:true
                    },()=>{
                        formss=this.state.checkedForms;
                        searchedForms=[];
                        this.checkedForms.current.setState({
                            forms:[]
                        },()=>{
                            formss.map((form,i)=>{
                                if(!form.state){
                                    
                                    return searchedForms.push(form)
                                }
                            });
                            this.checkedForms.current.setState({
                                forms:searchedForms
                            });
                        });
                        debugger
                    });
                }
            }else{
                
                this.searchValue='';
                if(e.currentTarget.checked){
                    this.setState({
                        checkedC:true
                    },()=>{
                        debugger
                        formss=this.state.checkedForms;
                        searchedForms=[];
                        this.checkedForms.current.setState({
                            forms:[]
                        },()=>{
                            formss.map((form,i)=>{
                                if(!form.state){
                                    
                                    return searchedForms.push(form)
                                }
                            });
                            this.checkedForms.current.setState({
                                forms:searchedForms
                            });
                            debugger
                        });
                    });
                }
            }
        }
    };
    searchNew = (e) =>{
        debugger
        e.preventDefault();
        var data=this.searchValue;
        var unchecked= document.querySelector('.unchecked-forms.active');
        debugger
        var formss;
        var searchedForms= this.state.searchedForms;
        var contratistaString='Contratista';
        var nominaString='Nomina';
       
        if(data!==''){
            if(this.filterState){
                this.setState({checkedB:false});
                this.setState({checkedC:false});
                this.setState({checkedA:true},()=>{
                    debugger
                    this.filterState=false;
                });
            }
            if(!this.searchState){
                debugger
                if(unchecked!==null){
                    this.setState({
                        uncheckedForms:this.uncheckedForms.current.state.forms
                    },()=>{
                        
                        this.searchState=true;
                        formss=this.state.uncheckedForms;
                        searchedForms=[];
                        this.uncheckedForms.current.setState({
                            forms:[]
                        },()=>{
                            formss.map((form,i)=>{
                                if(form.name.toUpperCase().indexOf(data.toUpperCase())>-1){
                                    
                                    return searchedForms.push(form)
                                }else{
                                    if(form.identification.indexOf(data)>-1){
                                        
                                        return searchedForms.push(form)
                                    }else{
                                        if(contratistaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                            
                                            form.request_type === 0? searchedForms.push(form) : searchedForms.push();
                                        }else if(nominaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                            form.request_type === 1? searchedForms.push(form) : searchedForms.push();
                                        }else{
                                            
                                        }
                                    }
                                }
                            });
                            this.uncheckedForms.current.setState({
                                forms:searchedForms
                            });
                            
                        });
                    });
                }else{
                    this.setState({
                        checkedForms:this.checkedForms.current.state.forms
                    },()=>{
                        
                        this.searchState=true;
                        formss=this.state.checkedForms;
                        searchedForms=[];
                        this.checkedForms.current.setState({
                            forms:[]
                        },()=>{
                            
                            formss.map((form,i)=>{
                                if(form.name.toUpperCase().indexOf(data.toUpperCase())>-1){
                                    
                                    return searchedForms.push(form)
                                }else{
                                    if(form.identification.indexOf(data)>-1){
                                        
                                        return searchedForms.push(form)
                                    }else{
                                        if(contratistaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                            
                                            form.request_type === 0? searchedForms.push(form) : searchedForms.push();
                                        }else if(nominaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                            form.request_type === 1? searchedForms.push(form) : searchedForms.push();
                                        }else{
                                            
                                        }
                                    }
                                }
                            });
                            
                            this.checkedForms.current.setState({
                                forms:searchedForms
                            });
                        });
                            
                    });
                }
            }else{
                debugger
                if(unchecked!==null){
                    formss=this.state.uncheckedForms;
                    searchedForms=[];
                    this.uncheckedForms.current.setState({
                        forms:[]
                    },()=>{
                        //search en unchecked array
                        formss.map((form,i)=>{
                            if(form.name.toUpperCase().indexOf(data.toUpperCase())>-1){
                                return searchedForms.push(form)
                            }else{
                                if(form.identification.indexOf(data)>-1){
                                    return searchedForms.push(form)
                                }else{
                                    if(contratistaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                        form.request_type === 0? searchedForms.push(form) : searchedForms.push();
                                    }else if(nominaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                        form.request_type === 1? searchedForms.push(form) : searchedForms.push();
                                    }else{
                                    }
                                }
                            }
                        })
                        this.uncheckedForms.current.setState({
                            forms:searchedForms
                        });
                    });
                }else{
                    //search en checked array
                    formss=this.state.checkedForms;
                    searchedForms=[];
                    this.checkedForms.current.setState({
                        forms:[]
                    },()=>{
                        //search en unchecked array
                        formss.map((form,i)=>{
                            if(form.name.toUpperCase().indexOf(data.toUpperCase())>-1){
                                
                                return searchedForms.push(form)
                            }else{
                                if(form.identification.indexOf(data)>-1){
                                    
                                    return searchedForms.push(form)
                                }else{
                                    if(contratistaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                        
                                        form.request_type === 0? searchedForms.push(form) : searchedForms.push();
                                    }else if(nominaString.toUpperCase().indexOf(data.toUpperCase())>-1){
                                        form.request_type === 1? searchedForms.push(form) : searchedForms.push();
                                    }else{
                                        
                                    }
                                }
                            }
                        });
                        this.checkedForms.current.setState({
                            forms:searchedForms
                        });
                    });
                }
            }
        }else{
            debugger
            if(unchecked!==null){
                this.uncheckedForms.current.setState({
                    forms:[]
                },()=>{
                    this.uncheckedForms.current.setState({
                        forms:this.state.uncheckedForms
                    })
                });
                this.searchState=false
            }else{

                this.checkedForms.current.setState({
                    forms:[]
                },()=>{
                    this.checkedForms.current.setState({
                        forms:this.state.checkedForms
                    })
                });
                this.searchState=false
            }
        }

    }
    getSearchVal = (e) =>{
        e.preventDefault();
        debugger
        this.searchValue = e.target.value
    }
    render(){ 
        var uncheckedView= this.state.uncheckedView;
        return(
            <React.Fragment>
                <div className="row align-items-center justify-content-center news-container">
                    <div className="no-gutters content-container-news">
                        <div className="header-menu">
                            <div className="btn-nav-news active btn-nav" onClick={(e)=>this.changeState(e,0)}>
                                <div className="btn-label absolute-center">
                                    Pendientes
                                </div>
                            </div>
                            <div className="btn-nav-checked inactive btn-nav" onClick={(e)=>this.changeState(e,1)}>
                                <div className="btn-label absolute-center">
                                    Historial
                                
                                </div>
                            </div>
                            <div className="btn-nav-close log-out" onClick={this.logOut}>
                                <div className="btn-label absolute-center">
                                    Cerrar sesión
                                </div>
                            </div>
                        </div>
                        <div className="news-content">
                            <div className="badge-container">
                                <div className="badge-content">
                                    <div className="badge-label">
                                        Pendientes:
                                    </div>
                                    <Badge className="news-unchecked" color="primary" badgeContent={this.state.uncheckedLength} max={999}>
                                    </Badge>
                                </div>
                                <div className="badge-content">
                                    <div className="badge-label">
                                        Certificadas:
                                    </div>
                                    <Badge className="news-aproved" badgeContent={this.state.checkedAprovedLength} max={999}>
                                    </Badge>
                                </div>
                                <div className="badge-content">
                                    <div className="badge-label">
                                        Rechazadas:
                                    </div>
                                    <Badge className="news-denied" color="secondary" badgeContent={this.state.checkedDeniedLength}>
                                    </Badge>
                                </div>
                            </div>
                            <div className="search-container">
                                <form className="search" noValidate autoComplete="off" onSubmit={(e)=>{this.searchNew(e)}}>
                                    <TextField id="outlined-basic" label="Buscar" variant="outlined" onChange={(e)=>{this.getSearchVal(e)}} />
                                </form>
                                <label className="search-btn" htmlFor="icon-button-file">
                                    <IconButton style={{color:'white'}} aria-label="upload picture" component="span" onClick={(e)=>{this.searchNew(e)}}>
                                        <SearchOutlined />
                                    </IconButton>
                                </label>
                            </div>
                            <div className="unchecked-forms active content-news">
                                <UncheckedNews 
                                    history={this.props.history}
                                    quantity={this.getUncheckedLength}
                                    ref={this.uncheckedForms}
                                />
                            </div>
                            <div className="checked-forms inactive content-news">
                                <CheckedNews 
                                    history={this.props.history}
                                    quantity={this.getCheckedLength}
                                    ref={this.checkedForms}
                                />
                            </div>
                        </div>
                        {
                            !uncheckedView &&
                            <div className="filter-content">
                            <FormGroup row>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={this.state.checkedA}
                                            onChange={(e)=>{this.handleChange(e,0);
                                            
                                                this.setState({checkedB:false});
                                                this.setState({checkedC:false})}}
                                            name="checkedA"
                                            color="primary"
                                        />
                                        }
                                    label="Todas"
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={this.state.checkedB}
                                            onChange={(e)=>{this.handleChange(e,1);
                                            
                                                this.setState({checkedC:false});
                                                this.setState({checkedA:false})}}
                                            name="checkedB"
                                            color="primary"
                                        />
                                        }
                                    label="Certificadas"
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={this.state.checkedC}
                                            onChange={(e)=>{this.handleChange(e,2);
                                                this.setState({checkedB:false});
                                                this.setState({checkedA:false})
                                            }}
                                            name="checkedC"
                                            color="primary"
                                        />
                                        }
                                    label="Rechazadas"
                                />
                            </FormGroup>
                        </div>
                        }
                        
                        <div className="toast-container">
                            <ToastContainer
                                position="bottom-right"
                                autoClose={15000}
                                hideProgressBar={false}
                                newestOnTop
                                closeOnClick
                                rtl={false}
                                limit={3}
                                pauseOnFocusLoss
                                draggable
                                pauseOnHover
                            />
                        </div>
                    </div>
                   
                    
                </div>
                
            </React.Fragment>
        )
         
    }
}
export default News;